import React, { createContext, useContext, useEffect, useMemo, useRef, useState } from 'react';
import mqtt from 'mqtt';
import { MQTT_BROKER_URL, MQTT_OPTIONS, TOPIC_GSR, TOPIC_HR } from '../config';

const MqttContext = createContext(null);

export const useMqtt = () => {
  return useContext(MqttContext);
};

export const MqttProvider = ({ children }) => {
  const clientRef = useRef(null);
  const [connected, setConnected] = useState(false);
  const [messages, setMessages] = useState({});

  useEffect(() => {
    const client = mqtt.connect(MQTT_BROKER_URL, MQTT_OPTIONS);
    clientRef.current = client;

    const handleConnect = () => {
      setConnected(true);
      // Subscribe to required topics
      client.subscribe([TOPIC_GSR, TOPIC_HR], (err) => {
        if (err) {
          // eslint-disable-next-line no-console
          console.error('MQTT subscribe error:', err);
        }
      });
    };

    const handleReconnect = () => {
      // eslint-disable-next-line no-console
      console.warn('MQTT reconnecting...');
    };

    const handleError = (err) => {
      // eslint-disable-next-line no-console
      console.error('MQTT error:', err);
    };

    const handleClose = () => {
      setConnected(false);
    };

    const handleMessage = (topic, payload) => {
      let value = undefined;
      try {
        const text = payload.toString();
        // Attempt JSON parse first, fallback to numeric
        if (text[0] === '{' || text[0] === '[') {
          const parsed = JSON.parse(text);
          value = typeof parsed === 'number' ? parsed : parsed.value ?? parsed.val ?? parsed.data ?? parsed;
        } else {
          const num = Number(text);
          value = Number.isNaN(num) ? text : num;
        }
      } catch (e) {
        value = payload.toString();
      }

      setMessages((prev) => ({ ...prev, [topic]: value }));
    };

    client.on('connect', handleConnect);
    client.on('reconnect', handleReconnect);
    client.on('error', handleError);
    client.on('close', handleClose);
    client.on('message', handleMessage);

    return () => {
      try {
        client.removeListener('connect', handleConnect);
        client.removeListener('reconnect', handleReconnect);
        client.removeListener('error', handleError);
        client.removeListener('close', handleClose);
        client.removeListener('message', handleMessage);
        if (client.connected) {
          client.end(true);
        } else {
          client.end();
        }
      } catch (_) {
        // ignore
      }
    };
  }, []);

  const value = useMemo(() => ({
    client: clientRef.current,
    connected,
    messages,
    gsr: messages[TOPIC_GSR],
    heartRate: messages[TOPIC_HR],
    publish: (topic, payload, opts) => clientRef.current?.publish?.(topic, payload, opts),
    subscribe: (topic) => clientRef.current?.subscribe?.(topic),
    unsubscribe: (topic) => clientRef.current?.unsubscribe?.(topic),
  }), [connected, messages]);

  return (
    <MqttContext.Provider value={value}>
      {children}
    </MqttContext.Provider>
  );
};

export default MqttContext;


