// MQTT configuration for the frontend client
// For public brokers with WebSocket, use ws:// or wss://
// Example public test broker: 'wss://test.mosquitto.org:8081/mqtt'

export const MQTT_BROKER_URL = process.env.REACT_APP_MQTT_URL || 'wss://test.mosquitto.org:8081/mqtt';

export const MQTT_OPTIONS = {
  keepalive: 30,
  reconnectPeriod: 2000,
  connectTimeout: 30_000,
  clean: true,
};

// Topics
export const TOPIC_GSR = process.env.REACT_APP_TOPIC_GSR || 'sensors/gsr';
export const TOPIC_HR = process.env.REACT_APP_TOPIC_HR || 'sensors/hr';


